<template>
    <div class="box">
        <div class="titleDown"></div>
        <div class="chartRank" ref="chart"></div>
    </div>
</template>

<script>
import * as echarts from 'echarts';

export default {
    data() {
        return {

        }
    },
    mounted() {
        this.initChart();//
    },
    methods: {
        initChart() {
            // domecharts
            var myChart = echarts.init(this.$refs.chart);
            // 
            var option = {
                title: {
                    left: 'center'
                },
                tooltip: {
                    trigger: 'item'
                },
                legend: {
                    top:'-2.2%',
                    orient: 'horizontal',
                    left: 'left',
                    textStyle:{
                        color:"#CCC"
                    }
                },
                series: [
                    {
                        name: 'Access From',
                        type: 'pie',
                        radius: '50%',
                        data: [
                            { value: 25, name: '' },
                            { value: 15, name: '' },
                            { value: 20, name: '' },
                            { value: 25, name: '' },
                            { value: 15, name: '' }
                        ],
                        emphasis: {
                            itemStyle: {
                                shadowBlur: 10,
                                shadowOffsetX: 0,
                                shadowColor: 'rgba(0, 0, 0, 0.5)'
                            }
                        }
                    }
                ]
            };
            // 
            myChart.setOption(option);
        }

    }

}
</script>

<style>
.box {
    margin: 12px;
    width: 300px;
    height: 298px;
    border-radius: 5%;
    background-color: #0f142a;
}

.titleDown {
    padding-top: 5px;
    padding-left: 12px;
    height: 30px;
    width: 100%;
    color: #74828b;
    font-size: 16px;
}

.chartRank {
    margin: 5px;
    height: 250px;
}
</style>