<template>
    <div class="header">
         <div class="title"></div>
         <div class="time">{{ times }}</div>
         <div class="indro"></div>
    </div>
</template>


<script>
export default {
  data() {
    return {
      times:'',//
    };
  },
  created() {
    this.getTimes()
  },
  // Vue
  beforeDestroy() {
    if (this.times) {
      clearInterval(this.getTimesInterval); 
    }
  },
  methods: {
    getTimes(){
      setInterval(this.getTimesInterval, 1000);
    },
    getTimesInterval:function() {
      let _this = this;
      let year = new Date().getFullYear(); //
      let month = new Date().getMonth() + 1; //
      let day = new Date().getDate(); //
      let hours = new Date().getHours(); //
      let minutes = new Date().getMinutes(); //
      let seconds = new Date().getSeconds(); //
      // 10  0
      if (hours < 10) {
        hours = "0" + hours;
      }
      if (minutes < 10) {
        minutes = "0" + minutes;
      }
      if (seconds < 10) {
        seconds = "0" + seconds;
      }
      //
      _this.times = year + "-" + month + "-" + day + " " + hours + ":" + minutes + ":" + seconds;
    },
  }
};
</script>


<style>

 .header{
    position: relative;
    width: 100%;
    height: 100px;
    background-color: #060610;
 }
 .title{
    position: absolute;
    top: 30%;
    left: 35%;
    color: #74828b;
    font-family: "Microsoft Yahei";
    font-size: 30px;
 }
 .time{
    position: absolute;
    right: 3%;
    top: 30%;
    color: #74828b;
    width: 300px;
    height: 40px;
    font-size: 16px;
 }
 .indro{
    position: absolute;
    right: 3%;
    top: 45%;
    color: #74828b;
    width: 300px;
    height: 40px;
    font-size: 16px;
 }
</style>